<template>
  <div class="setBox" v-loading="load">

    <div class="setName flex align between">
      <h1 class="setName">实名认证
        <b class="color-red font16" v-if="statueInfo && statueInfo.state == 'failed'">(认证失败：{{statueInfo.msg}})</b>
      </h1>
      <el-button :type="showEdit?'danger':'primary'" v-if="statueInfo && statueInfo.state == 'failed'" @click="showEdit = !showEdit">{{showEdit?'取消':'重新认证'}}</el-button>
    </div>

    <ShowData v-if="statueInfo && statueInfo.state && !showEdit" :info="statueInfo" />

    <div class="ifBox animated fadeIn" v-else>

      <div>
        <div class="name font16 marginB10">证件号码</div>
        <!-- <input type="text" placeholder="请输入证件号" v-model="certification.remark"> -->
        <el-input placeholder="请输入证件号" v-model="certification.remark" />
      </div>
      <!-- inputGroup -->

      <div class="space40"></div>

      <div class="imgBox flex align between">

        <div class="setItem">
          <div class="ietmTitle">上传身份证件正面（身份证、IDcard、护照）</div>
          <div class="space20"></div>
          <UploadImg actionPath="/proxyUrl/wallet/imgUpload" @loadDone="x=>setImg(x,'photo1')" />
        </div>
        <!-- setItem -->

        <div class="setItem">
          <div class="ietmTitle">上传身份证件反面（身份证、IDcard、护照）</div>
          <div class="space20"></div>
          <UploadImg actionPath="/proxyUrl/wallet/imgUpload" @loadDone="x=>setImg(x,'photo2')" />
        </div>
        <!-- setItem -->

      </div>
      <!-- imgBox -->

      <div class="space20"></div>

      <div class="textRight">
        <el-button type="primary" @click="sendData">提交认证</el-button>
      </div>

    </div>
    <!-- ifbox -->





  </div>
  <!-- setBox -->




</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import UploadImg from '@com/common/uploadImg'
  import ShowData from './showData'

  import {
    certificationSubmit,
    certificationStatue
  } from "../../data/apis";

  export default {
    data() {
      return {
        load: false,

        showEdit:false,//显示编辑

        imageUrl: '',
        certification: {
          photo1: null, //正面照片
          photo2: null, //反面照片
          remark: null //证件号码
        },

        statueInfo: null //认证信息
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    components: {
      UploadImg,
      ShowData
    },
    methods: {
      setImg(res, imgnum) { //设置图片值
        console.log(res, imgnum)
        this.certification[imgnum] = res.data;
      }, //setImg
      checkStatue() { //，检查认证状态
        certificationStatue().then(v => {
          console.log(v)
          this.statueInfo = v.data;
        })
      }, //checkCertificationStatue
      sendData() { //提交资料
        if (!this.certification.remark) {
          return this.$message.error('请输入证件号码');
        }
        if (!this.certification.photo1) {
          return this.$message.error('请上传正面证件照片');
        }
        if (!this.certification.photo2) {
          return this.$message.error('请上传反面证件照片');
        }

        this.load = true;

        certificationSubmit(this.certification).then(v => {
          this.$message.success(v.message);
          this.load = false;
          setTimeout(() => {
            location.reload();
          }, 1000);
        }) //certificationSubmit

      } //sendData
    },
    mounted() {
      this.checkStatue();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .setBox {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 4px;
    margin-bottom: 40px;
  }

  .setName {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: $them_color;
  }

</style>



