<template>
  <div class="setBox">

    <h1 class="setName">绑定谷歌验证码</h1>
    <div class="setItem">

      <div class="ietmTitle flex align">
        <span>绑定谷歌验证码</span>&nbsp;&nbsp;
        <el-tag type="warning" size="mini" v-if="statue=='wait'">待确认</el-tag>
        <el-tag type="success" size="mini" v-else-if="statue=='success'">已绑定</el-tag>
        <el-tag type="danger" size="mini" v-else>未绑定</el-tag>
      </div>

      <el-button type="primary" style="margin-top:20px;" v-loading="load" v-if="statue=='wait'" @click="getOldQr">获取认证二维码</el-button>

      <div class="space20" v-if="!statue"></div>

      <div class="textRight flex align" v-if="!statue">
        <el-input v-model="numberCode" placeholder="请输入验证码">
          <template slot="append">
            <GetCode :codeData="{number:userNumber,type:'openGoogle'}"/>
          </template>
        </el-input>
        <el-button type="primary" class="marginL10" @click="getCode" v-loading="load">立即绑定</el-button>
      </div>
      <!-- textRight -->

    </div>
    <!-- codeBox -->



    <el-dialog
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">

      <div class="codeBox">
        <div class="qrcode" ref="qrCodeUrl" id="qrCodeUrl"></div>
      </div>

      <div class="textCenter color-red font18 marginT10">请使用【谷歌验证器】扫码完成绑定</div>

      <div class="form textCenter" style="margin-top:30px;">
        <div style="width:50%;margin:0 auto;"><el-input v-model="gooleInput" class="textCenter" placeholder="请输入谷歌验证码"></el-input></div>
        <el-button type="primary" v-loading="load" @click="checkGooleDone" style="margin-top:20px;">确认绑定</el-button>
      </div>
    
    </el-dialog>


  </div>
  <!-- setItem -->



</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import {
    googleCheck,
    googleOpen,
    googleSubmit,
    getQR
  } from "../../data/apis";

  import GetCode from '@com/common/getCode'

  import QRCode from 'qrcodejs2'

  export default {
    data() {
      return {
        load:false,

        statue: false,
        codeAddr: null,
        dialogVisible:false,//显示扫码
        numberCode:'110112',//身份验证码

        gooleInput:null//谷歌验证码
      }
    },
    computed: {
      ...mapState(['userInfo']),
      userNumber(){
        return this.userInfo.user.area+this.userInfo.user.registerAccount
      }
    },
    components: {
      GetCode
    },
    methods: {
      checkGooleDone(){//验证后是否绑定
        if(!this.gooleInput){
          return this.$message.error('请输入谷歌验证码');
        }
        this.load = true;
        googleSubmit({code:this.gooleInput}).then(v=>{
          this.dialogVisible = false;
          this.load = false;
          this.$message.success(v.message);
          setTimeout(() => {
              location.reload();
          }, 1000);
        }).catch(e=>{
          this.load = false;
        })
      },//checkGooleDone
      getOldQr(){//后面获取二维码
        this.load = true;
        getQR().then(v=>{
          console.log(v)
          this.codeAddr = v.data;
          this.dialogVisible = true;
          this.load = false;
          setTimeout(() => {
            this.qrcode();
          }, 1000);
        }).catch(e=>{
          this.load = false;
        })
      },//getOldQr
      async getStatue() { //获取谷歌状态
        let res = await googleCheck();
        this.statue = res.data;
      }, //getStatue
      async getCode(){//获取扫码内容
        if(!this.numberCode){
            return this.$message.error('请输入验证码');
        }
        this.load = true;
        let codeText = await googleOpen({code:this.numberCode});
        this.codeAddr = codeText.data;
        this.dialogVisible = true;
        this.load = false;
        setTimeout(() => {
          this.qrcode();
        }, 1000);

      },//getCode
      async qrcode() { //生成二维码
        this.$refs.qrCodeUrl.innerHTML="";//先移除
        var qrcode = new QRCode(this.$refs.qrCodeUrl, {
          text: this.codeAddr,
          colorDark : "#2b3b4b",
          correctLevel: QRCode.CorrectLevel.H
        })
      }, //qrcode
      handleClose(done) {
        this.$confirm('请确认您已使用谷歌验证器扫码成功？')
          .then(_ => {
            done();
            setTimeout(() => {
                location.reload();
            }, 1000);
          })
          .catch(_ => {});
      }//handleClose
    },
    mounted() {
      this.getStatue();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .setBox {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 4px;
    margin-bottom: 40px;
  }

  .setName {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: $them_color;
  }
  
</style>



