<template>
  <div class="uploadImg" v-loading="load">
    
    <el-upload class="avatar-uploader" :headers="{Authorization:token}" :action="actionPath" :show-file-list="false"
      :on-success="handleAvatarSuccess" :on-error="handleAvatarFail" :before-upload="beforeAvatarUpload">
      <img v-if="imageUrl" :src="imageUrl" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'


  export default {
    props:['actionPath'],
    data() {
      return {
        imageUrl: '',
        load:false
      }
    },
    computed: {
      ...mapState(['userInfo','token'])
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        console.log(res);
        this.load = false;
        if(res.code!==200){
          this.$message.error(res.message);
        }else{
          this.$message.success(res.message);
          setTimeout(() => {
            this.$emit('loadDone',res)
          }, 500);
        }
      },
      beforeAvatarUpload(file) {
        // const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        this.load = true;
        return isLt2M;
      },
      handleAvatarFail(){
        this.load = false;
      }
    },
    mounted() {

    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">

</style>



