<template>
    <div class="animated fadeIn">

      <div class="name font16 marginB10">证件号码：{{info.remark}}</div>

      <div class="space20"></div>
  

      <div class="name font16 marginB10">证件信息</div>

      <div class="flex align imgs">
          <img :src="setImgUrl(info.photo1)" alt="">
          <img :src="setImgUrl(info.photo2)" alt="">
      </div>
    
    </div>
    <!-- setBox -->


</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'


  export default {
    props:['info'],
    data() {
      return {
        load:false,
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    components: {

    },
    methods: {
        
    },
    mounted() {

    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";
  .imgs{
    width: 100%;
    margin-top: 20px;
    img{
      display: block;
      width:45%;
      max-height: 200px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      &:nth-child(1){
        margin-right: 5%;
      }
      &:nth-child(2){
        margin-left: 5%;
      }
    }
  }

  .setBox {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 4px;
    margin-bottom: 40px;
  }

  .setName {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: $them_color;
  }

</style>



